* {
    padding: 0;
    margin: 0;
}

body {
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
}

body>main.container {
    height: 460px;
    width: 25%;
    border-radius: 10px;
    padding: 40px;
    box-shadow: 0px 0px 30px rgba(128, 128, 128, 0.295);
}

body>main.container>p.heading {
    color: black;
    font-size: 20px;
    font-weight: 500;
    text-align: center;
    margin: -15px 0px 0px 0px;
    text-decoration: wavy underline;
}

body>main.container>form.form {
    height: 360px;
}

body>main.container>form.form>section.inputs {
    display: flex;
    flex-direction: column;
}

body>main.container>form.form>section.inputs>label {
    margin: 35px 0px 6px 0px;
    font-size: 14px;
    color: rgb(0, 64, 255);
    text-decoration: dotted underline;
}

body>main.container>form.form>section.inputs>label.weight-label {
    margin: 25px 0px 6px 0px;
}

body> main.container> form.form> section.inputs > input.input-feild {
    height: 35px;
    padding: 0px 9px;
    font-size: 14px;
    outline: none;
}
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
}

body>main.container>form.form>section.buttons>button {
    width: 100%;
    height: 45px;
    background-color: #0077ee;
    margin: 55px 0px 0px;
    font-size: 16px;
    font-weight: 600;
    color: white;
    outline: none;
    border: 0px;
}

body>main.container>form.form>section.buttons>button.reload {
    margin: 28px 0px 0px 0px;
    background-color: #ededed;
    color: black;
}

body>main.container>form.form>section.BMI {
    margin: 25px 0px 0px 0px;
    height: 70px;
}

body>main.container>form.form>section.BMI>p.bmi {
    font-size: 14px;
    margin: 0px 0px 18px 0px;
}

body>main.container>form.form>section.BMI>p.remarks {
    font-weight: bold;
    color: orange;
    text-decoration: wavy underline;
    text-align: center;
    font-size: 14px;
}

@media screen and (max-width: 1200px) {
    body>main.container {
        width: 395px;
    }
}

@media screen and (max-width: 600px) {
    body>main.container {
        width: 295px;
    }
}

::-webkit-scrollbar {
    display: none;
}